import { createContext, useEffect,useState } from "react"
import axios from "axios"


export const AuthContext = createContext()
//createContext() :  ,    Context  

export const AuthContextProvider = ({children}) => {

    //currentUser setCurrentUser useState hook  currentUser   
    const [currentUser, setCurrentUser] = useState(JSON.parse(localStorage.getItem("user") || null ))
    //localStorage  "user"   JSON.parse()  JavaScript 
    //JSON.parse() : JSON  JavaScript   
    //useState() :   (state)  
    //               ,   state  ,   state    
    //             useState()      state 
    //             state   

    //login   
    const login = async(LoginId,LoginPw) => {
        //axios  post 
        const res = await axios.post("https://api-fvwt.onrender.com/api/auth/login", LoginId,LoginPw);
        setCurrentUser(res.data);
    }
    //logout  
    const logout = async(e)=>{
        //axios  post 
        await axios.post("https://api-fvwt.onrender.com/api/auth/logout");
        setCurrentUser(null);
        window.location.href="/";
    };


    //useEffect() :   side effect   
    //              side effect,         
    //              ex) API, DOM,  
    //                    
    //                 side effect ,   state 
    //                  ,    
    //                  useEffect()   state props   
    //                   , useEffect()      
    //                    ,    
    useEffect(()=> {
        //currentUser localStorage 
        localStorage.setItem("user", JSON.stringify(currentUser));
        //JSON.stringfy() : JavaScript JSON   
        //localStorage.setItem() : localStorage    
        //                             key,      
        //                         (,     . JSON )
    },[currentUser]);

    return(
        <AuthContext.Provider value={{currentUser,login,logout}}> 
            {children}
        </AuthContext.Provider>
        //AuthContext.Provider AuthContext     React
        //AuthContextProvider   
        //AuthContext.Provider 
        //currentUser, login, logout         
        //
        //, AuthContext.Provider      currentUser, login, logout  useContext      
    )   
}

//https://api-fvwt.onrender.com/api/auth/login
