import './Mypage.css'
import Navbar from '../Common/Navbar';
import { useContext, useState } from 'react';
import { AuthContext } from "../../context/Authcontext"

function Mypage() {

  const {currentUser ,logout} = useContext(AuthContext);


  const data=localStorage.getItem("user"); //    
  const parseData=JSON.parse(data); // JSON   JavaScript   
  //console.log(parseData.id);
  const username = parseData.id;
  const userPoint = parseData.point;
  //console.log(username);

    return (
      <>
      <div id='mypage'>
         <h1></h1>
         <div className="mpColWrap">
          <div className="mpRowWrap">
            <div className="mplabel"></div>
            <div id="userName">{username}</div>
          </div>
          <div className="mpRowWrap">
            <div className="mplabel"></div>
            <div id="userId">{username}</div>
          </div>
          <div className="mpRowWrap">
            <div className="mplabel"></div>
            <div id="pwBtn"></div>
          </div>
          <div className="mpRowWrap">
            <div className="mplabel"></div>
            <div className="mpPointWrap">
              <div id="userPoint">{userPoint}p</div>
              <div id="userPointTxt">(100p   .)</div>
            </div>
          </div>
          {currentUser ? <div id="logout" onClick={logout}></div> : <span></span>}
         </div>


      </div>
      <Navbar/>
      </>
        
        
    )
}

export default Mypage;
