import './Register.css'
import { React, useState} from "react";
import {useNavigate} from 'react-router-dom'
import axios from "axios";

function Register() {
    // useState
    //        

    const [err, setError] = useState(null);

    const navigate = useNavigate();

    //  
    const [userName, setUserid] = useState('');
    const [password, setPassword] = useState('');
    const [pwConfirm, setPwConfirm] = useState('');
    const [registerStatus, setRegisterStatus] = useState('');

    //   
    const [idMessage, setIdMessage] = useState("");
    const [pwMessage, setPwMsg] = useState("");
    const [pwConfirmMsg, setPwConfirmMsg] =useState("");

    // 
    const onChangeId = async (e) => {
        const userName = e.target.value; //    
        setUserid(userName); // useState 
        const idRegExp = /^[a-z0-9]{6,14}$/;
        if (!idRegExp.test(userName)) {
            setIdMessage("6-14     !");
        } else {
            try {
                const result = await axios.get(`https://api-fvwt.onrender.com/api/members?id=${userName}`);
                console.log(result.data.length)
                if(result.data.length) {
                    setIdMessage("  .");
                }else{
                    setIdMessage("");
                }

            }catch(err) {
                console.log(err);
            }
            
        }
    };

    const onChangePw = (e) => {
        const password = e.target.value;
        setPassword(password);
        const pwRegExp =
          /^(?=.*[a-zA-Z])(?=.*[0-9]).{6,20}$/;
        if (!pwRegExp.test(password)) {
          setPwMsg(
            "+  6  !"
          );
        } else {
          setPwMsg(" ");
        }
      };

    const onChangePwConfirm = (e) => {
      const pwConfirm = e.target.value;
      setPwConfirm(pwConfirm);
      if (password !== pwConfirm) {
        setPwConfirmMsg("  .");
      } else {
        setPwConfirmMsg(" ");
      }
    };


    //   
    const register = async (e) => {
        e.preventDefault();
        try{
            await axios.post('https://api-fvwt.onrender.com/api/auth/register',{
                id:userName,
                pass:password,
            })
            navigate("/");
        }catch(err) {
            setError(err.response.data);
        }
    }

    return(
        <div id="register">
            <div className="registerForm">
                <form>
                    <h4></h4>
                    <div id="idWrap">
                        <input 
                            className="textInput" 
                            type="text" 
                            name="userId" 
                            onChange={onChangeId}
                            placeholder=" 6-14  " required /><br/>
                        <span className="message"> {idMessage} </span>
                    </div>
                    
                    <div id="pwWrap">
                    <input 
                        className="textInput" 
                        type="password" 
                        name="password" 
                        onChange={onChangePw} 
                        placeholder=" 6-20     " required/> <br/>
                        <span className="message"> {pwMessage} </span>
                    </div>
                    
                    <div id="pwCfWrap">
                    <div id="pwCfTitle"> <span> *</span></div>
                    <input 
                        className="textInput" 
                        type="password" 
                        name="pwConfirm" 
                        value={pwConfirm}
                        onChange={onChangePwConfirm} 
                        placeholder="   ." required/> <br/>
                        <span className="message"> {pwConfirmMsg} </span>
                    </div>
                    
                    <div id="Btn">
                        <div id="cancelBtn" onClick={() => navigate("/")}>
                            
                        </div>
                        <div 
                            id="registerBtn" 
                            type="submit" 
                            onClick={register} ></div>
                    </div>

                </form>
            </div>
        </div>
    )
}

export default Register;