import './Login.css'
import React, { useState, useContext} from 'react';
import  Axios  from 'axios';
import id from '../../images/id.png'
import pw from '../../images/pw.png'
import {Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../context/Authcontext';

//  
function Login() {
   const [inputs, setInputs] = useState ({ //useState  inputs 
    id:"", //id pass   .
    pass:"",
   })
   
   //input    handleChange 
   const handleChange = e => {
    setInputs(prev => ({...prev, [e.target.name]: e.target.value}))
   }

    const navigate = useNavigate(); //  useNavigate hook   
    
    const {login} = useContext(AuthContext); //AuthContext login 

    // 
    const Login = async (e) => {
        console.log(inputs);

        e.preventDefault();
     try {
        await login(inputs); //inputs   login 
        navigate("/home");//    /home 
     } catch(err) {
        console.log(err);
       }}
       

    return(
        <div id="login">
            <h2>LOG IN</h2>
            <div>
                <div className="LoginInput" ><img className="loginIcon"src={id}/><input type='text' name='id' 
                 onChange={handleChange} placeholder="" /></div>
            </div>
            <div>
            
            <div className="LoginInput"><img className="loginIcon"src={pw}/><input type='password' name='pass' 
                 onChange={handleChange}  placeholder="" /></div>
            </div>
        
            <div type='button' id="LoginBtn" onClick={Login}></div>
            <div id="loginTxt">/  <span><Link to="/register"></Link></span></div>
        
        </div>
    )
}

export default Login;